import pupProtocol from "./protocol.vue";
import { USER_API } from "@/api/client";
import { mapState } from "vuex";
import moment from "moment";

export default {
  data() {
    return {
      activeType: 1,
      showPool: false,
      theme: 0,
      myPoolList: [],
      historyList: [],
      currencyReward: 0,
      stakeAmount: 0,
      stakingStatus: false,
      refreshLoading: false,
      isLoading: true,
      address: "",
      stakeId: "",
      token: "",
    };
  },
  components: {
    pupProtocol,
  },
  computed: {
    ...mapState(["local"]),
    locale() {
      return this.local.locale;
    },
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
  },
  async mounted() {
    this.setIcon();
    if (!this.isBitKeep) {
    //   this.address = "0x7068dd34531c8f7656b540e6290e352c880f6822";
      this.address = "0x3707766dbADE98CC3631B5427A8D9186db159154";
      this.getList();
      this.stakeHistory();
      this.handleGetToken();
    }
  },
  methods: {
    async getList() {
      //   this.address = await wallet.connect();
      const { data, status } = await USER_API.myPool({
        userid: this.address,
      });
      if (status == 1) {
        this.isLoading = false;
        this.refreshLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("staking.know"),
          confirmButtonColor: "$theme-light-colorPrimary",
        });
      }
      this.isLoading = false;
      this.refreshLoading = false;
      this.myPoolList = data.list;
    },
    async stakeHistory() {
      const { data, status } = await USER_API.stakeHistory({
        userid: this.address,
      });
      if (status == 1) {
        this.isLoading = false;
        this.refreshLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("staking.know"),
          confirmButtonColor: "$theme-light-colorPrimary",
        });
      }
      this.isLoading = false;
      this.refreshLoading = false;
      this.historyList = data.list;
    },
    async handleGetToken() {
      const { data, status } = await USER_API.handleGetToken({
        userid: this.address,
      });
      if (status == 1) {
        this.isLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("staking.know"),
          confirmButtonColor: "$theme-light-colorPrimary",
        });
      }
      this.token = data.token;
    },
    time(lockEndTime) {
      return moment(lockEndTime).format("YYYY-MM-DD HH:mm");
    },
    close(val) {
      if (val) {
        this.getList();
        this.handleGetToken();
      }
      this.showPool = false;
    },
    removeClick(item) {
      this.showPool = true;
      this.stakeAmount = item.stakeAmount;
      this.currencyReward = item.currencyReward;
      this.stakingStatus = item.isExpire;
      this.stakeId = item.id;
    },
    active(type) {
      this.activeType = type;
      if (this.activeType == 1) {
        this.getList();
      } else {
        this.stakeHistory();
      }
    },
    setIcon() {
      this.$store.commit('CHANGE_LANG',this.locale)
      this.isBitKeep &&
        BitKeepInvoke.onLoadReady(() => {
          BitKeepInvoke.getAddress(async (err, data) => {
            if (err) {
              return this.$toast(err);
            }
            this.addresses = data;
            this.address = this.addresses["eth"] || "--";
            this.getList();
            this.stakeHistory();
            this.handleGetToken();
          });
          BitKeepInvoke.setIconAction();
          BitKeepInvoke.setTitle(this.$t("staking.MyPoolsTitle"));
          //设置主题
          this.$nextTick(() => {
            BitKeepInvoke.appMode((err, res) => {
              let body = document.getElementsByTagName("body")[0];
              if (res == 1) {
                this.theme = 1;
                body.setAttribute("class", "theme-dark");
              } else {
                this.theme = 0;
                body.setAttribute("class", "theme-light");
              }
            });
          });
        });
    },
  },
};
